import React, { Component } from 'react';
import Table from '../table';
import './dashboard.css';

class Dashboard extends Component {
  constructor(props) {
    super(props);
    this.state = {
      datasets: []
    }
  }
  fetchMetadata() {
    return fetch(`/api/metadata/all?uid=${this.props.user.id}`, {
      headers: {
        'X-CSRF-TOKEN': this.props.user['CSRF-TOKEN']
      }
    })
    .then(res => res.json())
  }
  componentDidMount() {
    this.fetchMetadata().then((json) => {
      this.setState({
        datasets: json
      });
    });
  }

  render() {
    return (
      <div className='dashboard'>
        <h1 className='dashboard-title'>Dashboard</h1>
        <Table rows={this.state.datasets}/>
      </div>
    );
  }
}

export default Dashboard;



