import React, { Component } from 'react';
import './input.css';

class Input extends Component {

  render() {
    const { className, ...otherProps} = this.props;
    return (
      <input
        {...otherProps}
        className={['input', className].join(' ')}
      />
    );
  }
}

export default Input;



